import React, { Component } from 'react';

import './DonatePage.css';

import { Link } from "react-router-dom";

import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import Button from "../../components/Buttons/index.js";
import Modal from "../../components/Modal/Modal.js";

import VersionData from "../../components/Versions/VersionData.js";

import Icon from '../../components/Icon/Icon.js';

import githubIcon from '../../img/github.svg';


export default class DonatePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showGithubModal: false
        };
        this.setShowGithubModal = (show) => {
            this.setState({showGithubModal: show});
        }
    }

    T(en, ko) {
        if (this.props.language == "en") {
          return en;
        }
        else {
          return ko;
        }
    }

    render() {
        if (this.props.language == "en") {
            var donationButtons = (
                <>
                    <Button.LinkButton href="https://paypal.me/devhotteok" target="_blank">PayPal</Button.LinkButton>
                    <Button.LinkButton href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</Button.LinkButton>
                    <Button.LinkButton href="https://toss.me/devhotteok" target="_blank">Toss</Button.LinkButton>
                </>
            );
        }
        else {
            var donationButtons = (
                <>
                    <Button.LinkButton href="https://toss.me/devhotteok" target="_blank">토스(Toss)</Button.LinkButton>
                    <Button.LinkButton href="https://toon.at/donate/devhotteok" target="_blank">투네이션(Toonation)</Button.LinkButton>
                    <Button.LinkButton href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</Button.LinkButton>
                    <Button.LinkButton href="https://twip.kr/donate/devhotteok" target="_blank">트윕(Twip)</Button.LinkButton>
                </>
            );
        }
        var checkUrl = new URL(window.location.href);
        var downloadVersion = checkUrl.searchParams.get("download");
        if (VersionData.versionList().indexOf(downloadVersion) == -1) {
            downloadVersion = undefined;
        }
        if (downloadVersion != undefined) {
            if (VersionData.versionList().indexOf(downloadVersion) < 5) {
                var fileName = "TwitchLink-" + downloadVersion + ".exe";
            }
            else {
                var fileName = "TwitchLinkSetup-" + downloadVersion + ".exe";
            }
            this.download = (
                <div className="downloadInProgress">
                    <Label>{this.T("Thanks for using TwitchLink!", "TwitchLink를 이용해 주셔서 감사합니다.")}</Label>
                    <Label>{this.T("Your download will begin shortly.", "곧 다운로드가 시작됩니다.")}</Label>
                    <Button.LinkButton style={{margin: "10px auto"}} id="download" href={"https://github.com/devhotteok/TwitchLink/releases/download/" + downloadVersion + "/" + fileName}>
                        {this.T("If the download doesn't start automatically, click here.", "다운로드가 진행되지 않는다면 이곳을 누르세요.")}
                    </Button.LinkButton>
                    <Link className="LinkButton helpButton" to={"/help?lang=" + this.props.language} target="_blank">
                        {this.T("Having trouble installing?", "설치하는데 문제가 있나요?")}
                    </Link>
                </div>
            );
        }
        return (
            <div className="DonatePage">
                <PageFrame>
                    <Content.TitleBar>{this.T("Become a Sponsor", "후원하기")}</Content.TitleBar>
                    {this.download}
                    <Label className="donateInfo">{this.T("If you like the program, become a sponsor of TwitchLink!", "프로그램이 마음에 드신다면 TwitchLink의 개발을 도와주세요!")}</Label>
                    <Button.ButtonArea>
                        {donationButtons}
                    </Button.ButtonArea>
                    <Label className="thankyou">{this.T("Thanks For Your Help!", "후원해주셔서 감사합니다.")}</Label>
                    <Modal showModal={this.state.showGithubModal} modalTitle={this.T("Visit our GitHub!", "TwitchLink GitHub를 방문하세요!")} okCallback={() => {this.openGithub()}} okButton={this.T("OK", "확인")} cancelCallback={() => {this.closeGithubModal()}} cancelButton={this.T("Cancel", "취소")}>
                        {this.T(
                        "Visit our GitHub!",
                        "TwitchLink GitHub를 방문하세요!"
                        )}
                        <br/>
                        {this.T(
                        "Help us improve features, fix bugs, and contribute to TwitchLink open source!",
                        "기능 개선 및 버그 수정에 참여하고 TwitchLink 오픈소스에 기여하세요!"
                        )}
                        <Button.LinkButton id="githubButton" style={{marginTop: "20px"}} className="githubButton" href="https://github.com/devhotteok/TwitchLink" target="_blank">
                        <Icon src={githubIcon} alt="Language" style={{marginRight: "10px", filter: "invert(1)"}}/>
                        GitHub
                        </Button.LinkButton>
                    </Modal>
                </PageFrame>
            </div>
        );
    }

    openGithub() {
        this.closeGithubModal();
        document.getElementById("githubButton").click();
    }

    closeGithubModal() {
        this.setShowGithubModal(false);
    }

    componentDidMount() {
        if (this.download != undefined) {
            setTimeout(function () {
                document.getElementById("download").click();
            }, 2000);
        }
    }
}